function createData3(division, date, result, inspector, note) {
  let rowclass;
  if(division == '') {
    rowclass='date1';
  } else if(division == '') {
    rowclass='date2';
  } else if(division == '') {
    rowclass='date3';
  } else if(division == '') {
    rowclass='date4';
  } else {
    rowclass=''
  }
  return { division, date, result, inspector, note, rowclass };
}

